<template>
  <div
    class="mask"
    v-show="showCart && total > 0"
    @click="showCartChange"
  ></div>
  <div class="cart">
    <div class="cart__icon iconfont" @click="showCartChange">
      &#xe604;
      <span class="cart__icon__tag" v-if="total > 0">{{ total }}</span>
    </div>
    <div class="cart__price">&yen; {{ price }}</div>
    <router-link :to="{ name: 'Pay' }">
      <div class="cart__btn">去结算</div>
    </router-link>
  </div>
  <div class="cartList" v-show="showCart && total > 0">
    <div class="cartList__header">
      <div class="cartList__header__all" @click="() => cartCheckAll(shopId)">
        <span
          class="cartList__header__icon iconfont"
          v-html="checkAll ? '&#xe652;' : '&#xe66c;'"
        ></span>
        全选
      </div>
      <div class="cartList__header__clear">
        <span
          class="cartList__header__clear__btn"
          @click="() => clearCart(shopId)"
          >清空购物车</span
        >
      </div>
    </div>
    <template v-for="item in cartProductList" :key="item._id">
      <div class="productList__item" v-if="item.count > 0">
        <div
          class="productList__checked iconfont"
          v-html="item.check ? '&#xe652;' : '&#xe66c;'"
          @click="() => changeCartItemCheck(shopId, item._id)"
        ></div>
        <div class="productList__img">
          <img :src="item.imgUrl" />
        </div>
        <div class="productList__info">
          <div class="productList__info__name">{{ item.name }}</div>
          <div class="productList__info__price">&yen; {{ item.price }}</div>
        </div>
        <div class="productList__number">
          <span
            class="productList__number__minus iconfont"
            @click="() => changeItemToCart(shopId, item._id, item, -1)"
            >&#xe783;</span
          >
          <span class="productList__number__count">{{
            cartList?.[shopId]?.[item._id]?.count || 0
          }}</span>
          <span
            class="productList__number__add iconfont"
            @click="() => changeItemToCart(shopId, item._id, item, 1)"
            >&#xe658;</span
          >
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { computed, ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useCartEffect } from './changeItemToCart'
export default {
  name: 'Cart',
  setup() {
    const store = useStore()
    const route = useRoute()
    const shopId = route.params.id
    const cartList = store.state.cartList

    const showCart = ref(false)
    const showCartChange = () => {
      showCart.value = !showCart.value
    }

    const total = computed(() => {
      const productList = cartList[shopId]
      let count = 0
      if (productList) {
        for (const i in productList) {
          const product = productList[i]
          count += product.count
        }
      }
      return count
    })
    const price = computed(() => {
      const productList = cartList[shopId]
      let count = 0
      if (productList) {
        for (const i in productList) {
          const product = productList[i]
          if (product.check) {
            count += product.count * product.price
          }
        }
      }
      return count.toFixed(2)
    })
    const checkAll = computed(() => {
      const productList = cartList[shopId]
      let result = true
      if (productList) {
        for (const i in productList) {
          const product = productList[i]
          if (product.count > 0 && !product.check) {
            result = false
          }
        }
      }
      return result
    })
    const cartProductList = computed(() => {
      const productList = cartList[shopId] || []
      return productList
    })
    const changeCartItemCheck = (shopId, productId) => {
      store.commit('changeCartItemCheck', { shopId, productId })
    }
    const clearCart = shopId => {
      store.commit('clearCart', { shopId })
    }
    const cartCheckAll = shopId => {
      store.commit('cartCheckAll', { shopId })
    }

    const { changeItemToCart } = useCartEffect(store, cartList)

    return {
      total,
      price,
      checkAll,
      cartProductList,
      cartList,
      shopId,
      changeItemToCart,
      changeCartItemCheck,
      clearCart,
      cartCheckAll,
      showCart,
      showCartChange
    }
  }
}
</script>

<style lang="scss" scoped>
.cart {
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0.5rem;
  line-height: 0.5rem;
  background-color: #fff;
  &__icon {
    position: relative;
    width: 0.76rem;
    color: #0091ff;
    text-align: center;
    font-size: 0.2rem;
    cursor: pointer;
    &__tag {
      position: absolute;
      top: 0.02rem;
      right: 0.05rem;
      min-width: 0.2rem;
      height: 0.2rem;
      font-size: 0.16rem;
      text-align: center;
      line-height: 0.2rem;
      border-radius: 0.2rem;
      color: #fff;
      background-color: #e93b3b;
      padding: 0 0.05rem;
      transform: scale(0.5);
      transform-origin: center center;
    }
  }
  &__price {
    flex: 1;
    font-size: 0.14rem;
    color: #e93b3b;
  }
  &__btn {
    width: 0.76rem;
    text-align: center;
    color: #fff;
    background-color: #0091ff;
    font-size: 0.16rem;
  }
}
.cartList {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  right: 0;
  max-height: 50%;
  overflow-y: scroll;
  background-color: #fff;
  &__header {
    display: flex;
    height: 0.5rem;
    font-size: 0.14rem;
    color: #333;
    line-height: 0.25rem;
    border-bottom: 0.01rem solid #ccc;
    padding: 0.12rem;
    box-sizing: border-box;
    &__all {
      display: flex;
    }
    &__icon {
      color: #0091ff;
      font-size: 0.2rem;
      margin-right: 0.12rem;
    }
    &__clear {
      flex: 1;
      text-align: right;
      &__btn {
        display: inline-block;
      }
    }
  }
}
.productList {
  flex: 1;
  overflow-y: scroll;
  &__item {
    position: relative;
    display: flex;
    padding: 0.12rem;
  }
  &__checked {
    color: #0091ff;
    font-size: 0.2rem;
    line-height: 0.4rem;
    margin-right: 0.12rem;
  }
  &__img {
    width: 0.4rem;
    height: 0.4rem;
    background-color: #0091ff;
    margin-right: 0.12rem;
    & img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    &__name {
      font-size: 0.16rem;
      color: #333;
    }
    &__sales {
      color: #666;
      font-size: 0.12rem;
    }
    &__price {
      font-size: 0.14rem;
      font-weight: bold;
      color: #e93b3b;
    }
  }
  &__number {
    display: flex;
    justify-content: space-between;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0.6rem;
    height: 0.2rem;
    line-height: 0.2rem;
    text-align: center;
    font-size: 0.14rem;
    margin-right: 0.12rem;
    margin-bottom: 0.12rem;
    &__minus {
      color: #0091ff;
      font-size: 0.18rem;
    }
    &__add {
      color: #0091ff;
      font-size: 0.18rem;
    }
    &__count {
      font-size: 0.14rem;
      color: #333;
    }
  }
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
}
</style>
