<template>
  <div class="product">
    <div class="product__nav">
      <div class="product__nav__item" @click="goFood">食品</div>
      <div class="product__nav__item" @click="goDrink">饮品</div>
    </div>
    <div class="productList">
      <!-- 食品 -->
      <div id="food" class="productList__title">食品</div>
      <div class="productList__item" v-for="food in foodList" :key="food._id">
        <div class="productList__img">
          <img :src="food.imgUrl" />
        </div>
        <div class="productList__info">
          <div class="productList__info__name">{{ food.name }}</div>
          <div class="productList__info__sales">月售 {{ food.sales }}</div>
          <div class="productList__info__price">&yen; {{ food.price }}</div>
        </div>
        <div class="productList__number">
          <span
            class="productList__number__minus iconfont"
            @click="() => changeItemToCart(shopId, food._id, food, -1)"
            >&#xe783;</span
          >
          <span class="productList__number__count">{{
            cartList?.[shopId]?.[food._id]?.count || 0
          }}</span>
          <span
            class="productList__number__add iconfont"
            @click="() => changeItemToCart(shopId, food._id, food, 1)"
            >&#xe658;</span
          >
        </div>
      </div>
      <!-- 饮品 -->
      <div id="drink" class="productList__title">饮品</div>
      <div
        class="productList__item"
        v-for="drink in drinkList"
        :key="drink._id"
      >
        <div class="productList__img">
          <img :src="drink.imgUrl" alt="" />
        </div>
        <div class="productList__info">
          <div class="productList__info__name">{{ drink.name }}</div>
          <div class="productList__info__sales">月售 {{ drink.sales }}</div>
          <div class="productList__info__price">&yen; {{ drink.price }}</div>
        </div>
        <div class="productList__number">
          <span
            class="productList__number__minus iconfont"
            @click="() => changeItemToCart(shopId, drink._id, drink, -1)"
            >&#xe783;</span
          >
          <span class="productList__number__count">
            {{ cartList?.[shopId]?.[drink._id]?.count || 0 }}
          </span>
          <span
            class="productList__number__add iconfont"
            @click="() => changeItemToCart(shopId, drink._id, drink, 1)"
            >&#xe658;</span
          >
        </div>
      </div>
    </div>
  </div>
  <Cart />
</template>

<script>
import { reactive, toRefs, watchEffect } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { get } from '../../utils/request'
import Cart from './Cart'
import { useCartEffect } from './changeItemToCart'

export default {
  name: 'Product',
  components: { Cart },
  setup() {
    const store = useStore()
    const route = useRoute()
    const shopId = route.params.id
    const cartList = store.state.cartList

    const goFood = () => {
      document.querySelector('#food').scrollIntoView({ behavior: 'smooth' })
    }
    const goDrink = () => {
      document.querySelector('#drink').scrollIntoView({
        behavior: 'smooth'
        // block: 'start',
        // inline: 'nearest'
      })
    }
    const productFood = reactive({ foodList: [] })
    const productDrink = reactive({ drinkList: [] })
    const getProductFood = async () => {
      const resultFood = await get(`/api/productlist/${shopId}/food`)
      if (resultFood?.errno === 0 && resultFood?.data?.length) {
        productFood.foodList = resultFood.data
      }
    }
    const getProductDrink = async () => {
      const resultDrink = await get(`/api/productlist/${shopId}/drink`)
      if (resultDrink?.errno === 0 && resultDrink?.data?.length) {
        productDrink.drinkList = resultDrink.data
      }
    }
    watchEffect(() => {
      getProductFood()
      getProductDrink()
    })

    const { foodList } = toRefs(productFood)
    const { drinkList } = toRefs(productDrink)
    const { changeItemToCart } = useCartEffect(store, cartList)
    return {
      goFood,
      goDrink,
      foodList,
      drinkList,
      cartList,
      shopId,
      changeItemToCart
    }
  }
}
</script>

<style lang="scss" scoped>
.product {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0.5rem;
  background-color: #fff;
  &__nav {
    width: 0.76rem;
    height: 100%;
    background-color: #eee;
    overflow-y: scroll;
    font-size: 0.16rem;
    text-align: center;
    &__item {
      height: 0.5rem;
      line-height: 0.5rem;
      border-bottom: 0.01rem solid #ccc;
      box-sizing: border-box;
      color: #666;
      cursor: pointer;
    }
  }
}
.productList {
  flex: 1;
  overflow-y: scroll;
  &__title {
    height: 0.35rem;
    line-height: 0.35rem;
    color: #fff;
    background-color: #666;
    font-size: 0.2rem;
    font-weight: bold;
    padding: 0 0.12rem;
  }
  &__item {
    position: relative;
    display: flex;
    padding: 0.12rem;
    border-bottom: 0.01rem solid #666;
  }
  &__img {
    width: 0.68rem;
    height: 0.68rem;
    background-color: #0091ff;
    margin-right: 0.12rem;
    & img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    &__name {
      font-size: 0.18rem;
      color: #333;
    }
    &__sales {
      color: #666;
      font-size: 0.12rem;
    }
    &__price {
      font-size: 0.16rem;
      font-weight: bold;
      color: #e93b3b;
    }
  }
  &__number {
    display: flex;
    justify-content: space-between;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0.6rem;
    height: 0.2rem;
    line-height: 0.2rem;
    text-align: center;
    font-size: 0.14rem;
    margin-right: 0.12rem;
    margin-bottom: 0.12rem;
    &__minus {
      color: #0091ff;
      font-size: 0.18rem;
      cursor: pointer;
    }
    &__add {
      color: #0091ff;
      font-size: 0.18rem;
      cursor: pointer;
    }
    &__count {
      font-size: 0.14rem;
      color: #333;
    }
  }
}
</style>
